<template>
  <div id="show-blogs">
    <h2>博客总览</h2>
    <input v-model="search" type="text" placeholder="搜索" />
    <div v-for="blog in filteredBlogs" :key="blog.title" class="single-blog">
      <router-link v-bind:to="'/blog/' +blog.id">
        <h2 v-rainbow>{{blog.title}}</h2>
      </router-link>
      <article>{{blog.content}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    this.$http
      .get("https://vuedemo-ecc5a.firebaseio.com/posts.json")
      .then(function(data) {
        return data.json();
      })
      .then(function(data) {
        var blogsArray = [];
        for (let key in data) {
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;
      });
  },
  computed: {
    filteredBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  }
};
</script>

<style>
#show-blogs {
  background: #6677cc;
  padding: 50px;
}
h2 {
  text-align: center;
}
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}
#show-blogs a {
  color: #444;
  text-decoration: none;
}
input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



